import '../styles.scss'
import { installManifest, plistMap } from './constants'

export const downloadApp = document.querySelector('#download')
export const subTitles = document.querySelectorAll('.subtitle')
export const header = document.querySelector('.header')
export const dynamicHeader = document.querySelector('.dynamic-header')

window.addEventListener('scroll', () => {
  console.log('window.scrollY', window.scrollY)

  if (window.scrollY >= 1000) {
    if (dynamicHeader.classList.contains('dynamic-header--hidden'))
      dynamicHeader.classList.remove('dynamic-header--hidden')
  } else if (window.scrollY < 1000) {
    if (!dynamicHeader.classList.contains('dynamic-header--hidden'))
      dynamicHeader.classList.add('dynamic-header--hidden')
  }
})

const plist =
  plistMap[new URLSearchParams(window.location.search).get('plistId')]

const startInstall = () => {
  if (!navigator.userAgent.includes('Safari')) {
    alert('Устройство может не поддерживаться')
  }
}
if (plist) {
  subTitles.forEach((subTitle) => {
    subTitle.textContent = '«РСХБ ДБО ФЛ Учёт надоя» на iphone'
  })
  downloadApp.href = installManifest + encodeURIComponent(plist)
} else {
  subTitles.forEach((subTitle) => {
    subTitle.textContent = 'Ссылка на «РСХБ ДБО ФЛ Учёт надоя» не найдена'
  })
  downloadApp.classList.add('card__href--disabled')
}

downloadApp.addEventListener('click', startInstall)
window.addEventListener('load', () => downloadApp.click())
